import { webNavigation } from '../../platform'

import { Recorder } from './recorder'
import { isTopFrame } from '../utils'
import { navigated, captureEnabled, captureDisabled } from '../../store/recording/actions'
import { isHttp } from '../../utils/http'

import { WEB_NAVIGATION } from './types'

export class WebNavigationRecorder extends Recorder {
  url = 'about:blank'

  constructor(params) {
    super({ ...params, type: WEB_NAVIGATION })
  }

  async onEnable({ url }) {
    this.addListeners()

    this.dispatch(captureEnabled())

    this.handleNavigation({ url, timeStamp: Date.now() })
  }

  async onDisable() {
    this.removeListeners()

    this.dispatch(captureDisabled())
  }

  addListeners() {
    webNavigation.onBeforeNavigate.addListener(this.onBeforeNavigate)
    webNavigation.onCommitted.addListener(this.onCommited)
  }

  removeListeners() {
    webNavigation.onBeforeNavigate.removeListener(this.onBeforeNavigate)
    webNavigation.onCommitted.removeListener(this.onCommited)
  }

  isCurrentTab = (tabId) => tabId === this.tab.tabId
  isCurrentPage = (url) => url === this.url

  onBeforeNavigate = ({ tabId, frameId, url, timeStamp }) => {
    if (!this.isCurrentTab(tabId) || !isTopFrame(frameId)) {
      return
    }

    this.handleNavigation({ url, timeStamp })
  }

  handleNavigation({ url, timeStamp }) {
    if (this.isCurrentPage(url)) {
      return
    }

    this.url = url

    if (!isHttp(url)) {
      return
    }

    this.dispatch(
      navigated({
        url,
        timeStamp,
      })
    )
  }

  onCommited = ({ tabId, frameId, url }) => {
    if (!this.isCurrentTab(tabId) || !isTopFrame(frameId)) {
      return
    }

    this.url = url
  }
}
