import { WEB_NAVIGATION, createRecorder } from './recorders'

import { SchemeMonitor } from './utils/schemeMonitor'
import { TabMonitor } from './utils/tabMonitor'

class RecordingError extends Error {
  constructor(message) {
    super(message)
  }
}

const initRecorders = (type, params) => {
  return [createRecorder(WEB_NAVIGATION, params), createRecorder(type, params)]
}

class RecordingManager {
  recordings = {}

  init(store) {
    recordingManager.store = store
  }

  get({ tabId }) {
    return this.recordings[tabId]
  }

  start(tab, type, currentUrl) {
    if (this.get(tab) !== undefined) {
      throw new RecordingError(
        `Cannot start recording tab with id ${tab.tabId} because it is already being recorded.`
      )
    }

    const tabMonitor = new TabMonitor(this.store, tab)
    const schemeMonitor = new SchemeMonitor(tab)

    const recorders = initRecorders(type, {
      store: this.store,
      tab: tab,
      url: currentUrl,
      monitor: schemeMonitor,
    })

    this.recordings[tab.tabId] = {
      tab,
      tabMonitor,
      schemeMonitor,
      recorders,
    }

    tabMonitor.start()
  }

  stop(tab) {
    const recording = this.get(tab)

    if (recording === undefined) {
      throw new RecordingError(
        `Cannot stop recording tab with id ${tab.tabId} because it is not being recorded.`
      )
    }

    this.recordings[tab.tabId] = undefined

    recording.tabMonitor.stop()
    recording.schemeMonitor.stop()

    return Promise.all(recording.recorders.map((r) => r.disableCapture()))
  }

  enableCapture(tab, currentUrl) {
    const { recorders = [], schemeMonitor = null } = this.get(tab)

    if (recorders.length === 0) {
      throw new RecordingError(
        `Cannot enable capture for tab with id ${tab.tabId} because it is not being recorded.`
      )
    }

    schemeMonitor.start(currentUrl)

    return Promise.all(recorders.map((r) => r.enableCapture({ url: currentUrl })))
  }

  disableCapture(tab) {
    const { recorders = [] } = this.get(tab)

    if (recorders.length === 0) {
      throw new RecordingError(
        `Cannot disable capture for tab with id ${tab.tabId} because it is not being recorded.`
      )
    }

    return Promise.all(recorders.map((r) => r.disableCapture()))
  }

  getRecordings() {
    return Object.values(this.recordings).filter((a) => a !== undefined)
  }
}

export const recordingManager = new RecordingManager()
