import { canonicalizeHeaders, getContentDisposition } from './headers'

export const parseUrlEncoded = (data) =>
  [...new URLSearchParams(data).entries()].map(([name, value]) => ({ name, value }))

export const parseFormData = (boundary, data) => {
  let state = {
    index: 0,
    lines: data.split(/\r\n/gm),

    boundary: `--${boundary}`,
    end: `--${boundary}--`,

    result: null,
  }

  let parts = []

  while (!endOfFormData(state)) {
    state = parsePart(state)
    parts.push(state.result)
  }

  return parts.map(toParam)
}

const unquote = (str) => (str !== undefined ? str.replace(/^"|"$/gi, '') : str)

const toParam = ({ headers, body }) => {
  const contentDisposition = getContentDisposition(headers)
  const contentType = headers.find((header) => header.name === 'content-type')

  if (!contentDisposition) {
    return {}
  }

  return {
    name: unquote(contentDisposition.name),
    value: body,
    fileName: unquote(contentDisposition.filename),
    contentType: contentType ? contentType.value : undefined,
  }
}

const parsePart = (state) => {
  const boundary = line(state)

  if (boundary !== state.boundary) {
    throw new Error(
      `Expected to find boundary '${state.boundary}' but found '${boundary}' instead.`
    )
  }

  const headers = parseHeaders(nextLine(state))
  const body = parseBody(nextLine(headers))

  return withResult(body, {
    headers: headers.result,
    body: body.result,
  })
}

const parseHeaders = (state) => {
  let headers = []

  while (!endOfHeaders(state)) {
    const [name, value] = line(state)
      .split(':')
      .map((str) => str.trim())

    headers.push({
      name,
      value,
    })

    state = nextLine(state)
  }

  return withResult(state, canonicalizeHeaders(headers))
}

const parseBody = (state) => {
  let body = []

  while (!endOfBody(state)) {
    body.push(line(state))

    state = nextLine(state)
  }

  return withResult(state, body.join('\r\n'))
}

const endOfHeaders = (state) => line(state) === ''

const endOfBody = (state) => line(state) === state.boundary || endOfFormData(state)

const endOfFormData = (state) => line(state) === state.end

const line = (state) => state.lines[state.index]

const nextLine = (state) => ({
  ...state,
  index: state.index + 1,
})

const withResult = (state, result) => ({
  ...state,
  result,
})
