const platform = __platform
/**
 * Inspect events until the condition returns truthy, and then
 * unregister the event listener and resolve the promise with
 * the event details.
 *
 * @template T
 *
 * @param {chrome.events.Event<T>} event
 * @param {function(ev: T): boolean} condition
 * @param {...any} args
 * @return {Promise<T>}
 */
export const until = (event, condition, ...args) =>
  new Promise((resolve) => {
    const handler = (details) => {
      if (!condition(details)) {
        return
      }

      event.removeListener(handler)
      resolve(details)
    }

    event.addListener(handler, ...args)
  })

export const once = (event, ...args) => until(event, () => true, ...args)

/**
 * Transforms a platform function into a Promise returning async function.
 * @param {function(...any): any} func The platform function to transform
 * @param  {...any} extraArgs Any extra args to be added after any args passed via the returned function
 */
export const toAsync = (func, ...extraArgs) => (...args) =>
  new Promise((resolve, reject) =>
    func(...args, ...extraArgs, (...result) => {
      const error = runtime.lastError

      if (error) {
        reject({
          from: func.name,
          message: error.message,
        })

        return
      }

      if (result.length > 1) {
        resolve(result)

        return
      }

      resolve(result[0])
    })
  )

/**
 * Immediately call the given platform function as a Promise-returning async function.
 * @param {function(...any): any} func The function to call
 * @param  {...any} args Arguments to pass to the function
 */

export const callAsync = (func, ...args) => toAsync(func)(...args)

export const vendor = __vendor

// Rename to debug, since debugger is a keyword
export const debug = platform.debugger || {}

export const platformSupport = {
  debugger: platform.debugger !== undefined,
  webRequest: platform.webRequest !== undefined,
  webNavigation: platform.webNavigation !== undefined,

  extraInfoSpecs: __vendor === 'chrome' ? () => true : (spec) => !['extraHeaders'].includes(spec),
}

export const {
  webNavigation,
  tabs,
  downloads,
  webRequest,
  runtime,
  browserAction,
  storage,
  management,
} = platform
