import { debug, toAsync } from '../../platform'

const hasMinVersion = (min, current) => {
  const [minMajor, minMinor] = min.split('.')
  const [currentMajor, currentMinor] = current.split('.')

  if (currentMajor > minMajor) {
    return true
  }

  if (currentMajor == minMajor && currentMinor >= minMinor) {
    return true
  }

  return false
}

const minVersion = (minVersion, command, defaultValue) => async (target, ...args) => {
  const version = await Browser.getVersion(target)

  if (!hasMinVersion(minVersion, version.protocolVersion)) {
    return defaultValue
  }

  return await command(target, ...args)
}

const tryParseErrorMessage = ({ message }) => {
  try {
    return JSON.parse(message)
  } catch {
    return { message: message }
  }
}

const enrichErrorData = (method, promiseFunc) => (debugee, ...args) =>
  promiseFunc(debugee, ...args).catch((err) =>
    Promise.reject({ from: method, debugee, args, ...tryParseErrorMessage(err) })
  )

const command = (method) =>
  enrichErrorData(
    method,
    toAsync((debugee, ...args) => {
      debug.sendCommand(debugee, method, ...args)
    })
  )

const event = (name) => (debugee, callback) =>
  onEvent(debugee, (method, params) => {
    if (name != method) {
      return
    }

    callback(params)
  })

export const attach = toAsync(debug.attach)
export const detach = toAsync(debug.detach)

export const getTarget = (debugee) =>
  getAllTargets().then((targets) => targets.find((t) => t.tabId === debugee.tabId))

export const getAllTargets = toAsync(debug.getTargets)

export const isAttached = (debugee) => getTarget(debugee).then((t) => t !== undefined && t.attached)
export const isDetached = async (debugee) => isAttached(debugee).then((attached) => !attached)

const rootHandler = (type) => (debugee, callback) => {
  const onlyForCurrentDebugee = (source, ...args) => {
    if (source.tabId !== debugee.tabId) {
      return
    }

    callback(...args)
  }

  debug[type].addListener(onlyForCurrentDebugee)

  return () => debug[type].removeListener(onlyForCurrentDebugee)
}

export const onDetach = rootHandler('onDetach')
export const onEvent = rootHandler('onEvent')

export const Page = {
  reload: command('Page.reload'),
}

export const Network = {
  enable: command('Network.enable'),
  disable: command('Network.disable'),
  getResponseBody: command('Network.getResponseBody'),
  getRequestPostData: minVersion('1.3', command('Network.getRequestPostData'), {
    postData: undefined,
  }),

  requestWillBeSent: event('Network.requestWillBeSent'),
  responseReceived: event('Network.responseReceived'),
  loadingFinished: event('Network.loadingFinished'),
}

export const Browser = {
  getVersion: command('Browser.getVersion'),
}
