import { combineReducers } from 'redux'

import { controls } from './controls/reducer'
import { recording } from './recording/reducer'

const INITIAL_STATE = {
  error: null,
  userToken: null,
  activeWindowId: null,
  activeTabId: null,
}

export const rootReducer = combineReducers({
  controls,
  recording,
  generic: (state = INITIAL_STATE, action) => {
    switch (action.type) {
      case 'SET_USER_TOKEN':
        return {
          ...state,
          userToken: action.payload,
        }

      case 'RAISE_ERROR':
        return {
          ...state,
          error: action.payload,
        }

      case 'CLEAR_ERROR':
        return {
          ...state,
          error: null,
        }
      default:
        return state
    }
  },
})
