import { parseFormData, parseUrlEncoded } from '../../../utils/http/form'

export const requestBody = (contentType, data) => {
  switch (contentType.mimeType) {
    case 'application/x-www-form-urlencoded': {
      const params = typeof data === 'string' ? parseUrlEncoded(data) : data

      return {
        mimeType: contentType.mimeType,
        params: params.map((param) => ({
          name: encodeURIComponent(param.name),
          value: encodeURIComponent(param.value),
        })),
        text: typeof data === 'string' ? data : undefined,
      }
    }

    case 'multipart/form-data':
      return {
        mimeType: contentType.mimeType,
        params: typeof data === 'string' ? parseFormData(contentType.boundary, data) : data,
        text: typeof data === 'string' ? data : undefined,
      }

    default:
      return {
        mimeType: contentType.mimeType,
        text: data,
      }
  }
}
