import '../../config/reactotron'

import * as Sentry from '@sentry/browser'
import browser from 'webextension-polyfill'
import { store } from './store'
import { ConnectedStore } from './connectedStore'
import { ERROR_CODES } from './constants'
import { BackgroundServer } from './messaging/server'

import {
  START_RECORDING,
  STOP_RECORDING,
  RESET_RECORDING,
  DOWNLOAD_RECORDING,
  START_RECORDING_URL,
  MAKE_PROBE_REQUEST,
  CHECK_EXTERNAL_MESSAGING_SUPPORT,
} from '../store/controls/types'

import { recordingManager } from '../recording'
import { management, storage } from '../platform'

const onTabRemoved = (connectedStore) => (tabId) => {
  const { controls } = connectedStore.getState()

  if (controls.isRecording && tabId === controls.recordingTabId) {
    connectedStore.dispatchAction({ type: RESET_RECORDING })
  }
}

const onCommand = (connectedStore) => () => {
  const { controls } = connectedStore.getState()
  if (controls.isRecording) {
    connectedStore.dispatchAction({ type: STOP_RECORDING })
  } else {
    connectedStore.dispatchAction({ type: START_RECORDING })
  }
}

const allowedExternalActionTypes = [
  START_RECORDING_URL,
  STOP_RECORDING,
  MAKE_PROBE_REQUEST,
  CHECK_EXTERNAL_MESSAGING_SUPPORT,
]
const onExternalMessage = (connectedStore) =>
  function (action, sender, sendResponse) {
    if (action && action.type) {
      if (action.type === 'GET_RECORDED_HAR') {
        const { harRecords, currentRecording = 0 } = connectedStore.getState().controls

        return sendResponse({ payload: harRecords[currentRecording] })
      } else if (action.type === 'RESET_RECORDING') {
        return connectedStore
          .dispatchAction({ type: RESET_RECORDING })
          .then(() => sendResponse({ payload: { wasReset: true } }))
          .catch((error) =>
            sendResponse({
              error: { code: ERROR_CODES.UNKNOWN, message: error.message },
            })
          )
      } else if (action.type === 'DOWNLOAD_RECORDING') {
        return connectedStore
          .dispatchAction({ type: DOWNLOAD_RECORDING })
          .then((wasDownloaded) => sendResponse({ payload: { wasDownloaded } }))
          .catch((error) =>
            sendResponse({
              error: { code: ERROR_CODES.UNKNOWN, message: error.message },
            })
          )
      } else if (action.type === CHECK_EXTERNAL_MESSAGING_SUPPORT) {
        return sendResponse({ payload: { isSupported: true } })
      } else if (allowedExternalActionTypes.includes(action.type)) {
        return connectedStore
          .dispatchAction(action)
          .then((res) => sendResponse({ payload: res }))
          .catch((err) => sendResponse({ error: err }))
      }
    }

    sendResponse({
      error: {
        code: ERROR_CODES.BAD_REQUEST,
        message:
          action && action.type
            ? `Unsupported action: ${action.type} was dispatched.`
            : `An unsupported action was dispatched.`,
      },
    })
  }

const initializeDeveloperMode = () => {
  management.getSelf((extension) => {
    if (extension.installType !== 'development') {
      // Reset the cloud app url if we're not in development mode.
      storage.local.set({ cloudAppUrl: process.env.APP_REDIRECT_URL })
    }
  })
}

const initializeContextMenu = () => {
  // Adds an "Options" menu item when extension icon is right clicked
  browser.contextMenus.create({
    title: 'Options',
    contexts: ['browser_action'],
    onclick: function () {
      browser.runtime.openOptionsPage()
    },
  })
}

function main() {
  if (process.env.SENTRY_DSN) {
    Sentry.init({
      dsn: process.env.SENTRY_DSN,
      release: process.env.SENTRY_RELEASE,
    })
  }

  initializeDeveloperMode()

  const connectedStore = new ConnectedStore(store)
  connectedStore.init()

  window.backgroundServer = BackgroundServer.start(store)
  window.connectedStore = connectedStore

  recordingManager.init(store)

  __platform.tabs.onRemoved.addListener(onTabRemoved(connectedStore))
  __platform.runtime.onMessageExternal.addListener(onExternalMessage(connectedStore))

  if (__platform.commands) {
    __platform.commands.onCommand.addListener(onCommand(connectedStore))
  }

  if (process.env.VENDOR === 'firefox') {
    initializeContextMenu()
  }
}

main()
