import {
  START_RECORDING,
  STOP_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  RESET_RECORDING,
} from './types'

const INITIAL_STATE = {
  isRecording: false,
  isPaused: false,
  harRecords: [],
  currentRecording: 0,
}

export const controls = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case 'RAISE_ERROR':
      return { ...INITIAL_STATE, harRecords: state.harRecords }

    case START_RECORDING:
      return {
        ...state,
        error: null,
        isRecording: true,
        isPaused: false,
      }

    case STOP_RECORDING:
      return {
        ...state,
        isRecording: false,
        isPaused: false,
      }

    case PAUSE_RECORDING:
      return {
        ...state,
        isPaused: true,
      }

    case RESUME_RECORDING:
      return {
        ...state,
        isPaused: false,
      }

    case RESET_RECORDING:
      return { ...INITIAL_STATE, harRecords: state.harRecords }

    case 'ADD_HAR_RECORD':
      return {
        ...state,
        harRecords: [action.payload, ...state.harRecords].slice(0, 5),
        currentRecording: 0,
      }

    case 'CLEAR_HAR_RECORDS':
      return {
        ...state,
        harRecords: [],
        currentRecording: 0,
      }
    case 'SET_CURRENT_RECORDING':
      return {
        ...state,
        currentRecording: action.payload.index,
      }

    default:
      return state
  }
}
