export class Icon {
  constructor({ icon }) {
    this.rgba = [224, 62, 47, 1]
    this.opacity = 1
    this.delta = 0.1
    this.icon = icon
    this.blur = 2
    this.animationLoop = this.animationLoop.bind(this)
    this.startAnimation = this.startAnimation.bind(this)
  }

  animationLoop() {
    this.renderIcon()
    // requestAnimationFrame(this.animationLoop);
    this.timeout = window.setTimeout(this.animationLoop, 30)
  }

  renderIcon() {
    var image = new Image()
    image.src = this.icon

    image.onload = () => {
      this.ctx.clearRect(0, 0, image.width, image.height)

      this.iconSize = image.width
      this.radius = this.iconSize / 5
      this.centerX = image.width - this.radius - this.blur
      this.centerY = image.height - this.radius - this.blur
      this.canvas.width = image.width
      this.canvas.height = image.height

      this.arc = [this.centerX, this.centerY, this.radius, 0, 2 * Math.PI, false]

      this.ctx.drawImage(image, 0, 0)
      this.animateRecording()

      __platform.browserAction.setIcon({
        imageData: this.ctx.getImageData(0, 0, image.width, image.height),
      })
    }
  }

  stopAnimation() {
    if (!this.ctx) {
      return
    }

    window.clearTimeout(this.timeout)
    this.timeout = null
    this.isActive = false
    this.renderIcon()
  }

  startAnimation() {
    const canvas = document.createElement('canvas')
    this.canvas = canvas
    this.width = canvas.width
    this.height = canvas.height
    this.ctx = canvas.getContext('2d')
    this.isActive = true
    this.animationLoop()
  }

  animateRecording() {
    if (!this.isStarted) {
      this.startTime = window.performance.now()
      this.isStarted = true
    }

    if (this.opacity >= 1) {
      this.opacity = 1
      this.delta = -this.delta
    }

    if (this.opacity <= 0) {
      this.opacity = 0
      this.delta = -this.delta
    }

    this.ctx.beginPath()
    this.ctx.arc(...this.arc)
    this.ctx.fillStyle = '#4a4d67'
    this.ctx.lineWidth = 1
    this.ctx.strokeStyle = this.color
    this.ctx.fill()
    this.ctx.stroke()
    if (this.isActive) {
      this.opacity = parseFloat((this.delta + this.opacity).toFixed(2))
      this.color = `rgba(${this.rgba.join(',')})`

      this.ctx.beginPath()
      this.ctx.arc(...this.arc)

      this.ctx.shadowColor = this.color
      this.ctx.shadowBlur = this.blur
      this.ctx.fillStyle = this.color

      this.rgba = [...this.rgba.slice(0, -1), this.opacity]
      this.ctx.fillStyle = this.color
      this.ctx.lineWidth = 1
      this.ctx.strokeStyle = this.color
      this.ctx.fill()
      this.ctx.stroke()
    }
  }
}
