import { NULL } from './types'

export class Recorder {
  enabled = false

  constructor({ type = NULL, store, tab, monitor }) {
    this.type = type
    this.store = store
    this.monitor = monitor
    this.tab = tab
  }

  enableCapture(params) {
    if (this.enabled) {
      return Promise.resolve()
    }

    this.enabled = true

    return this.onEnable(params)
  }

  disableCapture(params) {
    if (!this.enabled) {
      return Promise.resolve()
    }

    this.enabled = false

    return this.onDisable(params)
  }

  async onEnable() {}

  async onDisable() {}

  dispatch(action, payload) {
    // Have we passed an action creator with its payload?
    if (typeof action === 'function' && payload !== undefined) {
      action = action(this.enrich(payload))
    }

    // Is the action a thunk?
    if (typeof action === 'function') {
      this.store.dispatch(action)

      return
    }

    // This "must" be a normal action-object...
    this.store.dispatch({
      ...action,
      payload: this.enrich(action.payload),
    })
  }

  enrich(payload) {
    return { ...payload, tab: this.tab, recorder: this.type }
  }
}
