export const isTopFrame = (frameId) => frameId === 0

/**
 * Makes sure we don't handle the same requests twice, which can happen
 * if a request is redirected. This effectively prevents the second redirect
 * request from being recorded, but keeps the same behavior as in previous
 * versions of the extension.
 */
export const deduplicate = (handler) => {
  let latch = new Set()

  return (req) => {
    const { requestId } = req

    if (latch.delete(requestId)) {
      return
    }

    latch.add(requestId)

    return handler(req)
  }
}
