import { webNavigation } from '../../platform'
import { isHttp } from '../../utils/http'
import { isTopFrame } from '.'

const toEventType = (current, target) => {
  if (current.http && !target.http) {
    return 'exit'
  }

  if (!current.http && target.http) {
    return 'enter'
  }

  return null
}

export class SchemeMonitor {
  constructor(tab) {
    this.tab = tab

    this.reset()
  }

  addListener(ev, handler) {
    this.listeners[ev] = this.listeners[ev] || []
    this.listeners[ev] = [...this.listeners[ev], handler]
  }

  removeListener(ev, handler) {
    if (ev in this.listeners) {
      this.listeners[ev] = this.listeners[ev].filter((fn) => fn !== handler)
    }
  }

  start(url) {
    this.current = { href: url, http: isHttp(url) }

    webNavigation.onCommitted.addListener(this.onCommitted)
  }

  stop() {
    webNavigation.onCommitted.removeListener(this.onCommitted)

    this.reset()
  }

  reset() {
    this.current = {
      href: 'about:blank',
      http: false,
    }

    this.listeners = {
      enter: [],
      exit: [],
    }
  }

  isVisitingHttp = () => isHttp(this.current.href)

  isCurrentTab = (tabId) => this.tab.tabId === tabId

  isCurrentUrl = (url) => this.current.href === url

  shouldProcess = ({ tabId, frameId, url }) =>
    this.isCurrentTab(tabId) && isTopFrame(frameId) && !this.isCurrentUrl(url)

  notify = (ev, data) => ev in this.listeners && this.listeners[ev].forEach((l) => l(data))

  onCommitted = (ev) => {
    if (!this.shouldProcess(ev)) {
      return
    }

    const target = {
      href: ev.url,
      http: isHttp(ev.url),
    }

    const type = toEventType(this.current, target)

    this.current = target

    if (ev === null) {
      return
    }

    this.notify(type, ev)
  }
}
