import { runtime } from '../../platform'

import {
  REQUEST_MSG,
  TRIGGER_MSG,
  GET_RECORDING_STATE,
  START_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  STOP_RECORDING,
  RESET_RECORDING,
  LIST_RECORDINGS,
  DOWNLOAD_RECORDING,
  OPEN_IN_CLOUD,
} from './types'

import {
  downloadRecording,
  openInCloud,
  stopRecording,
  pauseRecording,
  resumeRecording,
  resetRecording,
  recordTab,
} from '../../store/controls/actions'

import { RESERVED_PORTS } from '../connectedStore'
import { RecordingState } from '../../popup/store/recording/types'

export class BackgroundServer {
  static start(store) {
    return new BackgroundServer(store).init()
  }

  constructor(store) {
    this.store = store
  }

  init() {
    runtime.onConnect.addListener(this.onConnect)

    return this
  }

  onConnect = (port) => {
    if (RESERVED_PORTS.includes(port.name)) {
      return
    }

    port.onMessage.addListener(this.onMessage)
    port.onDisconnect.addListener(this.onDisconnect)
  }

  onDisconnect = (port) => {
    port.onMessage.removeListener(this.onMessage)
    port.onDisconnect.removeListener(this.onDisconnect)
  }

  tryRespond = (port, message) => {
    try {
      port.postMessage(message)
    } catch (err) {
      // Ignore errors trying to communicate with
      // a disconnected port.
    }
  }

  succeeded = (port, request) => (response) => {
    this.tryRespond(port, {
      succeeded: true,
      requestId: request.requestId,
      response: response,
    })
  }

  failed = (port, request) => (error = {}) => {
    this.tryRespond(port, {
      succeeded: false,
      requestId: request.requestId,
      error: {
        message: error.stack || error.toString() || error.message || 'unknown error',
      },
    })
  }

  onMessage = (msg, port) => {
    switch (msg.type) {
      case TRIGGER_MSG:
        this.trigger(msg.payload)
        break

      case REQUEST_MSG:
        this.handle(msg.payload).then(this.succeeded(port, msg)).catch(this.failed(port, msg))

        break
    }
  }

  trigger(payload) {
    switch (payload.type) {
      case DOWNLOAD_RECORDING:
        this.store.dispatch(downloadRecording(payload.index))
        break

      case OPEN_IN_CLOUD:
        this.store.dispatch(openInCloud(payload.index, payload.openIn))
        break
    }
  }

  asyncDispatch = (action) => {
    if (typeof action !== 'function') {
      this.store.dispatch(action)

      return Promise.resolve()
    }

    return new Promise((resolve, reject) => {
      this.store.dispatch((...args) => {
        action(...args)
          .then(resolve)
          .catch(reject)
      })
    })
  }

  async handle(payload) {
    switch (payload.type) {
      case GET_RECORDING_STATE: {
        const { controls, recording } = this.store.getState()

        if (recording.tab === null) {
          return {
            state: RecordingState.STOPPED,
          }
        }

        return {
          state: controls.isPaused ? RecordingState.PAUSED : RecordingState.STARTED,
          recordedTab: recording.tab,
        }
      }

      case START_RECORDING:
        return await this.asyncDispatch(recordTab(payload.opts))

      case PAUSE_RECORDING:
        return await this.asyncDispatch(pauseRecording())

      case RESUME_RECORDING:
        return await this.asyncDispatch(resumeRecording())

      case STOP_RECORDING:
        return await this.asyncDispatch(stopRecording())

      case RESET_RECORDING:
        return await this.asyncDispatch(resetRecording())

      case LIST_RECORDINGS:
        return this.store.getState().controls.harRecords
    }
  }
}
