import thunk from 'redux-thunk'
import { createStore, applyMiddleware, compose } from 'redux'

import { rootReducer } from '../store'

import {
  START_RECORDING,
  STOP_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  RESET_RECORDING,
} from '../store/controls/types'

import extensionControls from './extensionControls'

function extensionIconMiddleware() {
  return (next) => (action) => {
    switch (action.type) {
      case START_RECORDING:
      case RESUME_RECORDING:
        extensionControls.icon.startAnimation()
        break

      case RESET_RECORDING:
      case STOP_RECORDING:
      case PAUSE_RECORDING:
        extensionControls.icon.stopAnimation()
        break
    }

    return next(action)
  }
}

let store = applyMiddleware(thunk, extensionIconMiddleware)

if (process.env.NODE_ENV !== 'production') {
  // eslint-disable-next-line
  function logger({ getState }) {
    return (next) => (action) => {
      console.log('State before', getState())
      console.log('Will dispatch', action)
      const returnValue = next(action)
      console.log('State after', getState())
      return returnValue
    }
  }

  store = createStore(
    rootReducer,
    compose(
      applyMiddleware(thunk, extensionIconMiddleware, logger),
      globalThis.tron.createEnhancer()
    )
  )

  globalThis.tron.setupCommands(store)
} else {
  store = createStore(rootReducer, applyMiddleware(thunk, extensionIconMiddleware))
}

export { store }
