import { tabs as _tabs, runtime as _runtime, storage as _storage } from './index'

const toAsync = (fn) => (...args) =>
  new Promise((resolve, reject) => {
    fn(...args, (result) => {
      const lastError = _runtime.lastError

      if (lastError) {
        return reject(lastError)
      }

      resolve(result)
    })
  })

export const storage = {
  local: {
    get: toAsync(_storage.local.get.bind(_storage.local)),
    set: toAsync(_storage.local.set.bind(_storage.local)),
  },
}

export const tabs = {
  get: toAsync(_tabs.get),
  query: toAsync(_tabs.query),
  update: toAsync(_tabs.update),
}
