import { vendor } from '../../platform'

import { supportsRecorder, DEBUGGER, WEB_REQUEST, WEB_NAVIGATION, NULL } from './types'

import { WebRequestRecorder } from './webRequest'
import { DebuggerRecorder } from './debugger'
import { WebNavigationRecorder } from './webNavigation'
import { Recorder } from './recorder'

export { DEBUGGER, WEB_REQUEST, WEB_NAVIGATION, NULL }

class NotSupportedError extends Error {
  constructor(type, vendor) {
    super(`Platform ${vendor} does not support recording using ${type}.`)
  }
}

export const createRecorder = (type, params) => {
  if (!supportsRecorder(type)) {
    throw new NotSupportedError(type, vendor)
  }

  switch (type) {
    case DEBUGGER:
      return new DebuggerRecorder(params)

    case WEB_REQUEST:
      return new WebRequestRecorder(params)

    case WEB_NAVIGATION:
      return new WebNavigationRecorder(params)

    case NULL:
      return new Recorder(params)
  }
}
