import { platformSupport } from '../../platform'

export const DEBUGGER = 'debugger'
export const WEB_NAVIGATION = 'web-navigation'
export const WEB_REQUEST = 'web-request'
export const NULL = 'null'

export const supportsRecorder = (type) => {
  switch (type) {
    case DEBUGGER:
      return platformSupport.debugger

    case WEB_REQUEST:
      return platformSupport.webRequest

    case WEB_NAVIGATION:
      return platformSupport.webNavigation

    case NULL:
      return true

    default:
      return false
  }
}
