import extensionControls from '../../background/extensionControls'

import { tabs, downloads, callAsync } from '../../platform'
import { storage } from '../../platform/async'

import { action } from '../utils'

import {
  START_RECORDING,
  START_RECORDING_URL,
  MAKE_PROBE_REQUEST,
  STOP_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  RESET_RECORDING,
  DOWNLOAD_RECORDING,
  CLEAR_HAR_RECORDS,
} from './types'

import { DEBUGGER, WEB_REQUEST } from '../../recording/recorders'
import { toHAR } from '../../utils/har'
import { toTabRef } from '../../utils/tabs'
import { recordingManager } from '../../recording'
import {
  OPEN_IN_NEW_TAB,
  OPEN_IN_RECORDED_TAB,
  OPEN_IN_EXISTING_TAB,
  DEFAULT_SETTINGS,
} from '../../settings'

const toRecorderType = (includeResponseBody) => (includeResponseBody ? DEBUGGER : WEB_REQUEST)

export function recordTab({
  tab,
  downloadFile,
  correlateRequests,
  clearCache,
  openIn = OPEN_IN_NEW_TAB,
}) {
  return async (dispatch) => {
    if (clearCache) {
      await extensionControls.clearBrowserCache()
    }

    const recorderType = toRecorderType(correlateRequests)

    await recordingManager.start(tab, recorderType, tab.url)

    dispatch(
      startRecording({
        type: recorderType,
        tab: tab,
        url: tab.url,
        download: downloadFile,
        openIn,
      })
    )

    await recordingManager.enableCapture(tab, tab.url)
  }
}

export function startRecordingURL(action) {
  return async (dispatch) => {
    await extensionControls.navigateCurrentTab('about:blank')
    const currentTab = await extensionControls.getCurrentTab()
    await dispatch(
      recordTab({
        tab: toTabRef(currentTab),
        correlateRequests: true,
        clearCache: true,
        downloadFile: false,
        openIn: OPEN_IN_RECORDED_TAB,
      })
    )
    return extensionControls.updateCurrentTab({ url: action.payload.url })
  }
}

export function makeProbeRequest(action) {
  return () => {
    return fetch(action.payload.url)
      .then((res) => {
        if (res.ok) {
          return { isOk: res.ok, status: res.status }
        }

        return Promise.reject({
          isOk: res.ok,
          status: res.status,
          message: `The URL "${action.payload.url}" returned HTTP status ${res.status}`,
        })
      })
      .catch((error) =>
        error.status
          ? Promise.reject(error)
          : Promise.reject({
              isOk: false,
              status: -1,
              message: `The URL "${action.payload.url}" could not be reached`,
            })
      )
  }
}

export function addTestRecord(test) {
  return {
    type: 'ADD_HAR_RECORD',
    payload: test,
  }
}

export const openInCloud = (index, openIn) => async (dispatch, getState) => {
  const { recording, controls } = getState()

  if (index >= controls.harRecords.length) {
    return
  }

  dispatch(setCurrentRecording({ index }))

  await navigateToApp(recording.tab, openIn)
}

const navigateToApp = async (tab, openIn = OPEN_IN_NEW_TAB) => {
  const { cloudAppUrl } = await storage.local.get(DEFAULT_SETTINGS)

  let targetTab = tab && tab.tabId

  if (openIn === OPEN_IN_NEW_TAB || (openIn === OPEN_IN_RECORDED_TAB && targetTab === null)) {
    const createdTab = await extensionControls.openInNewTab({
      url: cloudAppUrl,
      active: true,
    })

    targetTab = createdTab.id
  } else if (openIn === OPEN_IN_EXISTING_TAB) {
    const tab = await extensionControls.openInExistingTab({
      url: cloudAppUrl,
      active: true,
    })

    targetTab = tab.id
  } else {
    await extensionControls.updateTabAndWait(targetTab, {
      url: cloudAppUrl,
      active: true,
    })
  }

  await callAsync(tabs.executeScript, targetTab, {
    runAt: 'document_end',
    file: 'inject-data.js',
  })
}

export function stopRecording() {
  return async (dispatch, getState) => {
    // TODO: Should be returned from recording manager instead
    const { recording } = getState()

    await recordingManager.stop(recording.tab)

    dispatch({ type: STOP_RECORDING })

    if (recording.timeline.length === 0) {
      return null
    }

    const har = toHAR(recording)

    dispatch(addTestRecord(har))

    await navigateToApp(recording.tab, recording.openIn)

    if (recording.download) {
      await dispatch(downloadRecording(0))
    }

    return har
  }
}

export function pauseRecording() {
  return async (dispatch, getState) => {
    const { recording } = getState()

    await recordingManager.disableCapture(recording.tab)

    dispatch(action(PAUSE_RECORDING, {}))
  }
}

export function resumeRecording() {
  return async (dispatch, getState) => {
    const { recording } = getState()
    const tab = await callAsync(tabs.get, recording.tab.tabId)

    await recordingManager.enableCapture(recording.tab, tab.url)

    dispatch(action(RESUME_RECORDING, {}))
  }
}

export function resetRecording() {
  return async (dispatch, getState) => {
    const { recording } = getState()

    await recordingManager.stop(recording.tab)

    dispatch({
      type: RESET_RECORDING,
    })
  }
}

export function downloadRecording(index = 0) {
  return async (dispatch, getState) => {
    const harRecords = getState().controls.harRecords

    if (index >= harRecords.length) {
      return false
    }

    const now = new Date()

    const filename = `new-recording_${now.getHours()}${now.getMinutes()}${now.getSeconds()}.har`
    const fileData = JSON.stringify(harRecords[index], null, 2)

    var blob = new File([fileData], filename, {
      type: 'octet/stream',
    })

    downloads.download({
      url: URL.createObjectURL(blob),
      filename,
    })

    // Heh..
    return true
  }
}

export const setCurrentRecording = action('SET_CURRENT_RECORDING')

export const clearHarRecords = action(CLEAR_HAR_RECORDS)
export const startRecording = action(START_RECORDING)

// Alias actions that are supposed to be triggered by popup or contentScript.
export const recordingActions = {
  [START_RECORDING]: recordTab,
  [STOP_RECORDING]: stopRecording,
  [PAUSE_RECORDING]: pauseRecording,
  [RESUME_RECORDING]: resumeRecording,
  [RESET_RECORDING]: resetRecording,
  [DOWNLOAD_RECORDING]: downloadRecording,
  [START_RECORDING_URL]: startRecordingURL,
  [MAKE_PROBE_REQUEST]: makeProbeRequest,
}
