import { tabs } from '../../platform'
import { interrupted } from '../../store/recording/actions'

export class TabMonitor {
  constructor(store, tab) {
    this.store = store
    this.tab = tab
  }

  start() {
    tabs.onRemoved.addListener(this.onRemoved)
  }

  stop() {
    tabs.onRemoved.removeListener(this.onRemoved)
  }

  onRemoved = (tabId) => {
    if (tabId !== this.tab.tabId) {
      return
    }

    this.store.dispatch(
      interrupted({
        tab: this.tab,
        reason: 'tab-closed',
      })
    )
  }
}
