import { parseUrlEncoded } from '../http/form'
import { RequestState } from '../../store/recording/types'

const toPageId = (id) => `page_${id}`
const toDateString = (timeStamp) => new Date(timeStamp).toISOString()

const toQueryParams = (url) => parseUrlEncoded(new URL(url).search)

const toPage = (page) => ({
  id: toPageId(page.id),
  url: page.url,
  title: page.url,
  startedDateTime: toDateString(page.timeStamp),
})

const toEntry = (item, record) => ({
  pageref: toPageId(item.page),
  startedDateTime: toDateString(record.request.timeStamp),
  request: {
    httpVersion: record.protocol,
    method: record.request.method,
    url: record.request.url,
    headers: record.request.headers,
    cookies: record.request.cookies,
    queryString: toQueryParams(record.request.url),
    postData: record.request.body || undefined,
  },
  response: {
    httpVersion: record.protocol,
    status: record.response.status,
    statusText: record.response.statusText,
    headers: record.response.headers,
    cookies: record.response.cookies,
    content: record.response.body || undefined,
  },
})

const hasFinished = (request) => request.status === RequestState.FINISHED

export const toHAR = ({ page, history, timeline, requests }) => {
  return {
    log: {
      version: '1.2', // HAR spec version
      creator: {
        name: process.env.APP_NAME,
        version: process.env.APP_VERSION,
      },
      pages: [...history, page].map(toPage),
      entries: timeline
        .filter((item) => hasFinished(requests[item.id]))
        .map((item) => toEntry(item, requests[item.id])),
    },
  }
}
