import { platformSupport, webRequest } from '../../platform'

import {
  requestSent,
  requestFinished,
  requestFailed,
  captureEnabled,
  captureDisabled,
} from '../../store/recording/actions'

import {
  canonicalizeHeaders,
  getResponseCookies,
  getRequestCookies,
  getContentType,
} from '../../utils/http/headers'

import { Recorder } from './recorder'
import { deduplicate } from '../utils'
import { RequestCorrelator } from '../utils/requestCorrelator'
import { requestBody } from './model/request'

import { WEB_REQUEST } from './types'

const createFilter = (tabId) => ({
  urls: ['http://*/*', 'https://*/*'],
  tabId,
})

const parseStatusLine = (statusLine) => {
  const [protocol, status, ...text] = statusLine.split(' ')

  return {
    protocol,
    status,
    text: text.join(' '),
  }
}

const toRequestBody = (headers, body) => {
  const contentType = getContentType(headers)

  if (!body) {
    return null
  }

  if ('formData' in body) {
    return requestBody(
      contentType,
      Object.entries(body.formData).flatMap(([name, values]) =>
        values.map((value) => ({ name, value }))
      )
    )
  }

  if ('raw' in body) {
    const decoder = new TextDecoder()

    // The WebRequest API will give you the body as a list of ArrayBuffers and,
    // if the content-type is multipart/form-data, File-items. Here we reconstruct
    // the string body by throwing away any File entries (they only contain the
    // path to the file selected by the user and we have no way of accessing the
    // file anyway), decoding the ArrayBuffers using UTF-8 and concatenating all
    // the parts into a single body.
    const bodyText = body.raw
      .filter((v) => v.bytes)
      .map((v) => decoder.decode(v.bytes))
      .join('')

    return requestBody(contentType, bodyText)
  }

  // I have no idea why we would get an error object here (the error message is
  // 'Unknown error', thank you very much). My theory is that a POST with
  // 'Content-Type: text/plain' and 'Content-Length: 0' causes an error when
  // the webRequest module tries to parse the output. A bug in chrome perhaps?
  if ('error' in body && contentType.mimeType === 'text/plain') {
    return requestBody(contentType, '')
  }

  return requestBody(contentType, body)
}

export class WebRequestRecorder extends Recorder {
  correlator = new RequestCorrelator()

  constructor(params) {
    super({ ...params, type: WEB_REQUEST })
  }

  async onEnable() {
    this.addListeners()

    this.dispatch(captureEnabled())
  }

  async onDisable() {
    this.removeListeners()

    this.dispatch(captureDisabled())
  }

  addListeners() {
    const filter = createFilter(this.tab.tabId)

    webRequest.onBeforeRequest.addListener(this.onBeforeRequest, filter, ['requestBody'])
    webRequest.onSendHeaders.addListener(
      this.onSendHeaders,
      filter,
      ['requestHeaders', 'extraHeaders'].filter(platformSupport.extraInfoSpecs)
    )

    webRequest.onCompleted.addListener(
      this.onCompleted,
      filter,
      ['responseHeaders', 'extraHeaders'].filter(platformSupport.extraInfoSpecs)
    )
  }

  removeListeners() {
    webRequest.onBeforeRequest.removeListener(this.onBeforeRequest)
    webRequest.onSendHeaders.removeListener(this.onSendHeaders)
    webRequest.onCompleted.removeListener(this.onCompleted)
  }

  onBeforeRequest = deduplicate(async ({ requestId, method, url, requestBody, timeStamp }) => {
    this.dispatch(
      requestSent({
        id: requestId,
        method,
        url,
      })
    )

    const headers = await this.correlator.waitOnValue(
      requestId,
      (pending) => pending.request.headers
    )

    if (!headers.success) {
      return
    }

    this.correlator.resolve(requestId, {
      request: {
        method,
        url,
        timeStamp,
        body: toRequestBody(headers.value, requestBody),
      },
    })
  })

  onSendHeaders = deduplicate(({ requestId, requestHeaders }) => {
    const headers = canonicalizeHeaders(requestHeaders)
    const cookies = getRequestCookies(headers)

    this.correlator.resolve(requestId, {
      request: {
        headers,
        cookies,
      },
    })
  })

  onCompleted = deduplicate(
    async ({ requestId, statusCode, statusLine, responseHeaders, timeStamp }) => {
      const status = parseStatusLine(statusLine)
      const headers = canonicalizeHeaders(responseHeaders)
      const cookies = getResponseCookies(headers)

      this.correlator.resolve(requestId, {
        protocol: status.protocol,
        response: {
          status: statusCode,
          statusText: status.text,
          timeStamp,
          headers,
          cookies,
          body: null,
        },
      })

      const result = await this.correlator.waitUntilDone(requestId)

      if (!result.success) {
        this.dispatch(requestFailed(result.error))

        return
      }

      this.dispatch(requestFinished(result.value))
    }
  )
}
