import { Icon } from './extension/icon'
import { webNavigation, callAsync } from '../platform'
import { tabs } from '../platform'

const getCurrentTab = () =>
  new Promise((resolve, reject) => {
    __platform.tabs.query({ active: true, currentWindow: true }, (tabs) => {
      if (tabs.length > 0) {
        return resolve(tabs[0])
      }

      reject({ message: 'Invalid tab is selected.', name: 'Error' })
    })
  })

const updateTab = (tabID, props) =>
  new Promise((resolve) => {
    __platform.tabs.update(tabID, props, resolve)
  })

const updateCurrentTab = (props) =>
  new Promise((resolve) => {
    getCurrentTab().then((tab) => {
      if (tab) {
        __platform.tabs.update(tab.id, props, () => {
          resolve(tab)
        })
      }
    })
  })

export const openInNewTab = async (props) => {
  return new Promise((resolve) => {
    tabs.create(props, (tab) => {
      resolve(tab)
    })
  })
}

export const openInExistingTab = async (props) => {
  const parsedUrl = new URL(props.url)
  const pattern = `${parsedUrl.protocol}//${parsedUrl.host}/*`

  const [existingTab] = await callAsync(tabs.query, { url: pattern })

  if (existingTab === undefined) {
    return await openInNewTab(props)
  }

  return updateTabAndWait(existingTab.id, props)
}

export const updateTabAndWait = async (tabID, props) => {
  return new Promise((resolve) => {
    const onUpdated = (updatedTabID, updatedProps, tab) => {
      if (updatedTabID === tabID) {
        tabs.onUpdated.removeListener(onUpdated)
        resolve(tab)
      }
    }

    tabs.onUpdated.addListener(onUpdated)
    updateTab(tabID, props)
  })
}

const navigateCurrentTab = (url) =>
  new Promise((resolve, reject) => {
    const onCommitted = (ev) => {
      webNavigation.onCommitted.removeListener(onCommitted)
      if (!ev) {
        return reject()
      }

      if (ev.url === url) {
        getCurrentTab().then(resolve)
      }
    }

    webNavigation.onCommitted.addListener(onCommitted)
    updateCurrentTab({ url })
  })

export default {
  icon: new Icon({
    icon: 'images/app-icon@48.png',
  }),
  updateTab,
  getCurrentTab,
  updateCurrentTab,
  openInNewTab,
  openInExistingTab,
  updateTabAndWait,
  navigateCurrentTab,
  clearBrowserCache() {
    const oneWeekInMilliseconds = 1000 * 60 * 60 * 24 * 7
    const settings = {
      since: new Date().getTime() - oneWeekInMilliseconds,
      originTypes: {
        unprotectedWeb: true,
      },
    }
    return new Promise((resolve) => {
      __platform.browsingData.removeCache(settings, resolve)
    })
  },
}
