import { isHttp } from '../../utils/http'

import { START_RECORDING, STOP_RECORDING, RESET_RECORDING } from '../controls/types'

import {
  NAVIGATED_TO_NEW_PAGE,
  REQUEST_SENT,
  REQUEST_FINISHED,
  RequestState,
  INTERRUPTED,
  TAB_CLOSED,
  CAPTURE_ENABLED,
  CAPTURE_DISABLED,
} from './types'

const INITIAL_STATE = {
  capturing: false,
  nextId: 0,
  tab: null,
  page: null,
  history: [],
  timeline: [],
  requests: {},
  download: false,
}

const isCapturing = (state) => state.capturing
const isNewRequest = (state, payload) => state.requests[payload.id] === undefined
const hasNavigatedToPage = (state) => state.page !== null

export const recording = (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case START_RECORDING:
      return {
        ...INITIAL_STATE,
        tab: payload.tab,
        download: payload.download,
        openIn: payload.openIn,
      }

    case RESET_RECORDING:
    case STOP_RECORDING:
      return {
        ...INITIAL_STATE,
      }

    case CAPTURE_ENABLED:
      return { ...state, capturing: true }

    case CAPTURE_DISABLED:
    case INTERRUPTED:
    case TAB_CLOSED:
      return { ...state, capturing: false }

    case NAVIGATED_TO_NEW_PAGE:
      return {
        ...state,
        nextId: state.nextId + 1,
        page: { id: state.nextId + 1, url: payload.url, timeStamp: payload.timeStamp },
        history: state.page ? [...state.history, state.page] : [],
      }

    case REQUEST_SENT:
      if (!isCapturing(state) || !isHttp(payload.url) || !hasNavigatedToPage(state)) {
        return state
      }

      return {
        ...state,
        requests: {
          ...state.requests,
          [payload.id]: {
            status: RequestState.PENDING,
            ...payload,
          },
        },
        timeline: [...state.timeline, { page: state.page.id, id: payload.id }],
      }

    case REQUEST_FINISHED:
      if (isNewRequest(state, payload) && !isCapturing(state)) {
        return state
      }

      if (!isHttp(payload.request.url)) {
        return state
      }

      return {
        ...state,
        requests: {
          ...state.requests,
          [payload.id]: { status: RequestState.FINISHED, ...payload },
        },
      }

    default:
      return state
  }
}
