/** The possible states of a request in a recording */
export const RequestState = {
  PENDING: 'PENDING',
  FINISHED: 'FINISHED',
}

// Lifecycle events from a recorder
export const CAPTURE_ENABLED = 'recorder/capture-enabled'
export const CAPTURE_DISABLED = 'recorder/capture-disabled'

// Events that are recorded
export const NAVIGATED_TO_NEW_PAGE = 'recording/navigated-to-new-page'

export const REQUEST_SENT = 'recording/request-sent'
export const REQUEST_FAILED = 'recording/request-failed'
export const REQUEST_FINISHED = 'recording/request-finished'

// Events interrupting the recording process in some way
export const TAB_CLOSED = 'recording/tab-closed'
export const INTERRUPTED = 'recording/interrupted'
