/**
 * A key-value pair representing a single header
 * @typedef {{ name: string, value: string}} Header
 */

/**
 * Converts header names to canonical lowercase form
 * @param {Header[]} headers
 * @returns {Header[]} The same headers, but where names are lowercased
 */
export const canonicalizeHeaders = (headers) =>
  headers.map(({ name, ...rest }) => ({ name: name.toLowerCase(), ...rest }))

const toCamelCase = (str) => {
  const lowerCaseChars = [str.charAt(0).toLowerCase()]

  for (let i = 1; i < str.length; ++i) {
    const char = str.charAt(i)

    if (char === char.toUpperCase()) {
      break
    }

    lowerCaseChars.push(char.toLowerCase())
  }

  return (
    lowerCaseChars.join('') +
    str
      .substring(lowerCaseChars.length)
      .replace(/[_-](\w)?/i, (_, nextChar = '') => nextChar.toUpperCase())
  )
}

const splitKeyValuePairs = (value) =>
  value
    .split(';')
    .map((str) => str.trim())
    .map((str) => str.split('='))

export const parseContentType = (value) => {
  const [mimeType, ...rest] = value.split(';').map((s) => s.trim())
  const params = Object.fromEntries(rest.map((s) => s.split('=')))

  return {
    ...params,
    mimeType,
    value,
  }
}

export const parseContentDisposition = (value) => {
  // Psst... don't let anyone know we are doing this! :P
  const { mimeType, ...rest } = parseContentType(value)

  return {
    ...rest,
    disposition: mimeType,
  }
}

export const parseCookieHeader = (value) =>
  splitKeyValuePairs(value).map(([name, value]) => ({
    name,
    value,
  }))

export const parseSetCookieHeader = (headerValue) => {
  const [[name, value], ...params] = splitKeyValuePairs(headerValue)

  const cookie = {
    name,
    value,
  }

  return params.reduce((result, [paramName, paramValue]) => {
    // The HAR-spec uses camel-case for cookie params, so we
    // do too for convenience
    const property = toCamelCase(paramName)

    // Undefined value means that it is a switch (e.g. HttpOnly) and
    // it's presence means that it's value is true
    const value = typeof paramValue === undefined ? true : paramValue

    return {
      ...result,
      [property]: value,
    }
  }, cookie)
}

const byName = (name) => (header) => header.name === name

export const getContentLength = (headers) => {
  const header = headers.find(byName('content-length'))

  if (!header) {
    return 0
  }

  return parseInt(header.value)
}

export const getContentType = (headers) => {
  const header = headers.find(byName('content-type'))

  if (!header) {
    return parseContentType('application/octet-stream')
  }

  return parseContentType(header.value)
}

export const getContentDisposition = (headers) => {
  const header = headers.find(byName('content-disposition'))

  if (!header) {
    return null
  }

  return parseContentType(header.value)
}

export const getRequestCookies = (headers) =>
  headers.filter(byName('cookie')).flatMap((header) => parseCookieHeader(header.value))

export const getResponseCookies = (headers) =>
  headers.filter(byName('set-cookie')).map((header) => parseSetCookieHeader(header.value))
