import { deferred } from '../../utils/deferred'
import { timeout, TIMED_OUT } from '../../utils/timeout'

/** Indicates that a value will be available at a later time */
export const pending = Symbol('pending')

const isDone = (obj) =>
  Object.entries(obj).every(([, value]) => {
    if (isObject(value)) {
      return isDone(value)
    }

    return value !== pending
  })

const isObject = (value) => value !== null && !Array.isArray(value) && typeof value === 'object'

const patch = (target, source) => {
  let result = {}

  for (const [name, value] of Object.entries(target)) {
    result[name] = value

    if (source[name] === undefined) {
      continue
    }

    if (isObject(value) && isObject(source[name])) {
      result[name] = patch(value, source[name])

      continue
    }

    result[name] = source[name]
  }

  return result
}

const emptyRequest = {
  protocol: pending,
  request: {
    method: pending,
    url: pending,
    headers: pending,
    cookies: pending,
    body: pending,
    timeStamp: pending,
  },
  response: {
    status: pending,
    statusText: pending,
    headers: pending,
    cookies: pending,
    body: pending,
    timeStamp: pending,
  },
}

/**
 * RequestCorrelator handles out-of-order events of requests, allowing the user
 * to wait for all parts of a request to complete.
 */
export class RequestCorrelator {
  pendingRequests = {}

  getRequestState = (requestId) => {
    this.pendingRequests[requestId] = this.pendingRequests[requestId] || {
      done: false,
      requestId: requestId,
      request: emptyRequest,
      progress: deferred(),
    }

    return this.pendingRequests[requestId]
  }

  clearRequest = (requestId) => {
    this.pendingRequests[requestId] = undefined
  }

  handleError = (requestId, error) => {
    const state = this.getRequestState(requestId)

    if (error === TIMED_OUT) {
      return {
        success: false,
        error: { code: TIMED_OUT, request: { id: requestId, ...patch(state.request, {}) } },
      }
    }

    throw error
  }

  getRequestURL = (requestId) => {
    return this.getRequestState(requestId).request.request.url
  }

  resolveRedirect = (requestId, url, partial) => {
    if (this.pendingRequests[requestId] && this.getRequestURL(requestId) === url) {
      this.resolve(requestId, partial)
    }
  }

  /** Resolve values in request by assigning any properties given in the second */
  resolve = (requestId, partial) => {
    const pending = this.getRequestState(requestId)
    const patched = patch(pending.request, partial)

    this.pendingRequests[requestId] = {
      done: isDone(patched),
      requestId: requestId,
      request: patched,
      progress: deferred(),
    }

    pending.progress.resolve(this.pendingRequests[requestId])
  }

  /** Wait until any progress is made, i.e. the resolve function was called. */
  waitOnProgress = (state) => timeout(state.progress, 60000)

  /** Wait until the value selected by the accessor parameter is no longer pending, and return its value. */
  waitOnValue = async (requestId, accessor) => {
    try {
      let state = this.getRequestState(requestId)

      while (accessor(state.request) === pending) {
        state = await this.waitOnProgress(state)
      }

      return { success: true, value: accessor(state.request) }
    } catch (error) {
      return this.handleError(requestId, error)
    }
  }

  /** Wait until all values have been resolved and then return the entire request */
  waitUntilDone = async (requestId) => {
    try {
      let state = this.getRequestState(requestId)

      while (!state.done) {
        state = await this.waitOnProgress(state)
      }

      return { success: true, value: { id: requestId, ...state.request } }
    } catch (error) {
      return this.handleError(requestId, error)
    } finally {
      this.clearRequest(requestId)
    }
  }
}
