import { attach, detach, onDetach, Network, Page, isDetached } from '../../utils/debugging/client'

import {
  requestSent,
  requestFinished,
  interrupted,
  requestFailed,
  captureEnabled,
  captureDisabled,
} from '../../store/recording/actions'

import {
  canonicalizeHeaders,
  getRequestCookies,
  getResponseCookies,
  getContentType,
  getContentLength,
} from '../../utils/http/headers'

import { requestBody } from './model/request'

import { DEBUGGER } from './types'
import { Recorder } from './recorder'
import { RequestCorrelator } from '../utils/requestCorrelator'
import { tabs, callAsync } from '../../platform'
import { deduplicate } from '../utils'

const splitOnNewLine = (str) => str.split(/\r?\n/gm)

const toHeaderList = (headers) =>
  canonicalizeHeaders(
    Object.entries(headers).flatMap(([name, values]) =>
      splitOnNewLine(values).map((value) => ({ name, value }))
    )
  )

const toMilliseconds = (seconds) => seconds * 1000

const parseProtocol = (protocol) => {
  switch (protocol) {
    // For some reason, http/2 uses h2 but http/1.x uses the standard format (http/1.1)
    // See: https://source.chromium.org/chromium/chromium/src/+/master:net/http/http_response_info.cc;l=510?q=h2%5C%22&ss=chromium&originalUrl=https:%2F%2Fcs.chromium.org%2F
    case 'h2':
      return 'http/2'

    default:
      return protocol
  }
}

const SUPPORTED_RESPONSE_BODIES = new Set([
  'application/json',
  'application/xml',
  'application/x-www-form-urlencoded',
  'multipart/form-data',
  'text/html',
  'text/xml',
])

const hasPostData = (headers, request) =>
  typeof request.postData === 'string' || request.hasPostData || getContentLength(headers) > 0

export class DebuggerRecorder extends Recorder {
  listeners = []

  correlator = new RequestCorrelator()

  constructor(params) {
    super({ ...params, type: DEBUGGER })

    this.url = params.url
    this.debugee = { tabId: params.tab.tabId }
  }

  async onEnable() {
    this.monitor.addListener('enter', this.onSchemeEnter)

    this.dispatch(captureEnabled())

    if (!this.monitor.isVisitingHttp()) {
      return
    }

    await this.attach()
  }

  async onDisable() {
    this.monitor.removeListener('enter', this.onSchemeEnter)

    await this.detach()

    this.dispatch(captureDisabled())
  }

  async attach() {
    await attach(this.debugee, '1.2')

    this.addListeners(this.debugee)

    await Network.enable(this.debugee, {})
  }

  async detach() {
    this.removeListeners()

    if (await isDetached(this.debugee)) {
      return
    }

    await detach(this.debugee).catch(() => {
      /* We are already detached, so ignore the error here */
    })
  }

  addListeners(debugee) {
    this.listeners = [
      Network.requestWillBeSent(debugee, this.onRequestWillBeSent),
      Network.responseReceived(debugee, this.onResponseReceived),
      Network.loadingFinished(debugee, this.onLoadingFinished),

      onDetach(debugee, this.onDetached),
    ]
  }

  removeListeners() {
    this.listeners.forEach((cancellation) => cancellation())
    this.listeners = []
  }

  async getRequestBody(requestId, request) {
    const { postData, hasPostData } = request

    if (postData) {
      return postData
    }

    if (!hasPostData) {
      return null
    }

    try {
      const result = await Network.getRequestPostData(this.debugee, {
        requestId,
      })

      return result.postData
    } catch (err) {
      // There was no postData for the given request, event though
      // the event told us that there would be. :S
      return null
    }
  }

  async getResponseBody(requestId) {
    try {
      return await Network.getResponseBody(this.debugee, { requestId })
    } catch (err) {
      // There was no response body for the given request.
      return null
    }
  }

  onRequestWillBeSent = async ({ requestId, wallTime, redirectResponse, request }) => {
    if (redirectResponse) {
      this.onResponseReceived({ requestId, response: redirectResponse })

      return
    }

    this.dispatch(
      requestSent({
        id: requestId,
        method: request.method,
        url: request.url,
      })
    )

    const headers = toHeaderList(request.headers)

    const body = hasPostData(headers, request)
      ? await this.getRequestBody(requestId, request)
      : null

    this.correlator.resolve(requestId, {
      request: {
        method: request.method,
        url: request.url,
        headers: headers,
        cookies: [],
        body: body && requestBody(getContentType(headers), body),
        timeStamp: toMilliseconds(wallTime),
      },
    })
  }

  onResponseReceived = deduplicate(async ({ requestId, response }) => {
    if (response.requestHeaders) {
      const requestHeaders = toHeaderList(response.requestHeaders)

      this.correlator.resolveRedirect(requestId, response.url, {
        request: {
          headers: requestHeaders,
          cookies: getRequestCookies(requestHeaders),
        },
      })
    }

    const requestTime = await this.correlator.waitOnValue(requestId, (p) => p.request.timeStamp)

    if (!requestTime.success) {
      return
    }

    const responseHeaders = toHeaderList(response.headers)
    const timeStamp =
      response.time && response.time.receiveHeadersEnd
        ? requestTime.value + toMilliseconds(response.timing.receiveHeadersEnd)
        : Date.now()

    this.correlator.resolve(requestId, {
      protocol: parseProtocol(response.protocol),
      response: {
        status: response.status,
        statusText: response.statusText,
        headers: responseHeaders,
        cookies: getResponseCookies(responseHeaders),
        timeStamp,
      },
    })
  })

  onLoadingFinished = deduplicate(async ({ requestId }) => {
    const headers = await this.correlator.waitOnValue(
      requestId,
      (pending) => pending.response.headers
    )

    if (!headers.success) {
      return
    }

    const contentType = getContentType(headers.value)

    const responseBody = SUPPORTED_RESPONSE_BODIES.has(contentType.mimeType)
      ? await this.getResponseBody(requestId)
      : null

    this.correlator.resolve(requestId, {
      response: {
        body: responseBody
          ? {
              mimeType: contentType.mimeType || contentType.value,
              text: responseBody.body,
              encoding: responseBody.base64Encoded ? 'base64' : undefined,
            }
          : null,
      },
    })

    const result = await this.correlator.waitUntilDone(requestId)

    if (!result.success) {
      this.dispatch(requestFailed(result.error))

      return
    }

    this.dispatch(requestFinished(result.value))
  })

  onDetached = async (reason) => {
    switch (reason) {
      case 'target_closed': {
        const openTabs = await callAsync(tabs.query, { windowId: this.tab.windowId })

        // If we got this event because the tab was closed, ignore it.
        // We are already monitoring tab-close in elsewhere.
        if (!openTabs.some((t) => t.id === this.tab.tabId)) {
          return
        }

        this.dispatch(interrupted, { reason: 'invalid-target' })
        break
      }

      case 'canceled_by_user':
        this.dispatch(interrupted, { reason: 'user-canceled' })
        break
    }
  }

  onSchemeEnter = async () => {
    await this.attach()

    // When this event has fired, the requests to fetch the page
    // have already been sent. In order to capture then we need to
    // reload the page. This is ugly as heck, but until I can
    // figure out a better way it will have to be good enough.
    await Page.reload(this.debugee, {})
  }
}
