import { runtime } from '../platform'
import { actions } from '../store/controls'
import { parseError } from '../utils/parseError'

export const RESERVED_PORTS = ['CONNECT_TO_STORE', 'STATE_PROVIDER', 'injected-data']

const hasOwnProperty = (obj, property) => Object.prototype.hasOwnProperty.call(obj, property)

export class ConnectedStore {
  constructor(store) {
    this.store = store
  }

  onPortConnect = (port) => {
    if (!RESERVED_PORTS.includes(port.name)) {
      return
    }

    let disconnected = false

    const onDisconnect = (port) => {
      disconnected = true

      port.onMessage.removeListener(onMessage)
      port.onDisconnect.removeListener(onDisconnect)

      cancel()
    }

    const onMessage = (action) => {
      this.dispatchAction(action)
    }

    const onDispatch = () => {
      if (disconnected) {
        return
      }

      port.postMessage(this.store.getState())
    }

    const cancel = this.store.subscribe(onDispatch)

    port.onMessage.addListener(onMessage)
    port.onDisconnect.addListener(onDisconnect)

    port.postMessage(this.store.getState())
  }

  getState() {
    return this.store.getState()
  }

  dispatchAction(action) {
    if (this.store.getState().generic.error) {
      this.store.dispatch({ type: 'CLEAR_ERROR' })
    }

    if (hasOwnProperty(actions, action.type)) {
      return this.store.dispatch(actions[action.type](action)).catch((error) => {
        this.store.dispatch({
          type: 'RAISE_ERROR',
          payload: {
            details: parseError(error),
          },
        })
        throw error
      })
    } else {
      return new Promise((resolve) => {
        this.store.dispatch(action)
        resolve()
      })
    }
  }

  init() {
    runtime.onConnect.addListener(this.onPortConnect)
  }
}
