import { action } from '../utils'

import {
  NAVIGATED_TO_NEW_PAGE,
  REQUEST_SENT,
  REQUEST_FAILED,
  REQUEST_FINISHED,
  TAB_CLOSED,
  INTERRUPTED,
  CAPTURE_ENABLED,
  CAPTURE_DISABLED,
} from './types'

import { stopRecording, resetRecording } from '../controls/actions'

export const captureEnabled = action(CAPTURE_ENABLED)
export const captureDisabled = action(CAPTURE_DISABLED)

export const navigated = action(NAVIGATED_TO_NEW_PAGE)

export const requestSent = action(REQUEST_SENT)
export const requestFailed = action(REQUEST_FAILED)
export const requestFinished = action(REQUEST_FINISHED)

export const tabClosed = action(TAB_CLOSED)

export const interrupted = (payload) => async (dispatch) => {
  dispatch(action(INTERRUPTED, payload))

  switch (payload.reason) {
    case 'tab-closed':
      dispatch(resetRecording())
      break

    case 'invalid-target': {
      dispatch(stopRecording())
      break
    }

    case 'user-canceled':
      dispatch(stopRecording())
      break
  }
}
